/**
 * 文字列をキャメルケースへ変換
 */
function toCamelCase(str) {
	if (typeof str !== 'string') return str;

	// 順序変更用のアンダースコアを削除
	str = str.replace('_', '');

	const strs = str.split('-');
	if (strs.length === 0) return str;

	return strs
		.map(function (word, index) {
			// 先頭の文字は小文字にする場合
			// if (index === 0) {return word.toLowerCase();}

			return word.charAt(0).toUpperCase() + word.slice(1).toLowerCase();
		})
		.join('');
}

/**
 * クラス名を持つかどうか調べる
 */
export const migrateIconName = (iconName) => {
	if (!iconName) return iconName;

	if (null !== iconName.match(/^fa/) && -1 !== iconName.indexOf(' fa-')) {
		// fa-solid fa-earth-asia → FasEarthAsia
		iconName = iconName.replace('fa-solid fa-', 'Fas-');
		iconName = iconName.replace('fas fa-', 'Fas-');
		iconName = iconName.replace('fa-regular fa-', 'Far-');
		iconName = iconName.replace('far fa-', 'Far-');
		iconName = iconName.replace('fa-brand fa-', 'Fab-');
		iconName = iconName.replace('fab fa-', 'Fab-');

		return toCamelCase(iconName);
	}

	if (iconName.startsWith('arkb-svg')) {
		const migrateList = {
			// icon-file-empty
			// 'arkb-svg-logo': 'LsSwellLogo',
			'arkb-svg-point': 'LsLightbulb',
			'arkb-svg-thumb_up': 'LsThumbUp',
			'arkb-svg-thumb_down': 'LsThumbDown',
			'arkb-svg-comment': 'LsChat',
			'arkb-svg-batsu': 'LsX',
			'arkb-svg-posted': 'LsTime',
			'arkb-svg-warning': 'LsWarning',
			'arkb-svg-star': 'LsStarFull',
		};

		if (migrateList.hasOwnProperty(iconName)) {
			return migrateList[iconName];
		}

		iconName = iconName.replace('arkb-svg-', 'ls-');

		return toCamelCase(iconName);
	}

	return iconName;
};
