/**
 * @WordPress dependencies
 */
import { RichText, InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import {
	RawHTML, //renderToString
} from '@wordpress/element';

/**
 * @Internal dependencies
 */
import { migrateIconName } from '@helper/migrate';
import IconContentV1 from './components/IconContentV1';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

/**
 * metadata
 */
const blockName = 'ark-block-boxLink';

const oldSupports = {
	anchor: true,
	className: false,
	reusable: false,
};

const saveBefore220 = ({ attributes }) => {
	const {
		textAlign,
		useIcon,
		icon,
		iconSize,
		iconColor,
		iconHtml,
		layout,
		imgUrl,
		title,
		htag,
		href,
		rel,
		isNewTab,
		more,
		showMoreArrow,
		opacity,
		ariaLabel,
		iconV1,
		// figureStyle,
	} = attributes;

	const oldSvg = iconV1 || '';
	// oldSvg = oldSvg.replace(/<span.*?<\/span>/g, ''); // arkb の span にはクラスがあることに注意

	// const iconSize = Number.isSafeInteger(attributes.iconSize)
	// 	? attributes.iconSize + 'px'
	// 	: attributes.iconSize;

	console.log('iconSize', attributes.iconSize, iconSize);
	// console.log('figureStyle', figureStyle);
	// --arkb-boxlink_icon_size:120px;color:#ff0000

	let wrapClass = 'arkb-boxLink';
	if (Number.isSafeInteger(iconSize)) {
		wrapClass += ' arkb-columns__item';
	}

	const blockClass = classnames(blockName, wrapClass, `-${layout}`, {
		'has-text-align-center': 'center' === textAlign,
	});

	// 縦並びか横並びかを変数化
	// const isVertical = 'vertical' === layout;

	const attrClass = attributes.className || '';
	const isBannerStyle = -1 !== attrClass.indexOf('is-style-banner');

	let figureContent = null;
	if (isBannerStyle) {
		const layerStyle = {};
		if (50 !== opacity) layerStyle.opacity = '' + (opacity * 0.01).toFixed(2);

		figureContent = (
			<>
				{imgUrl && <RawHTML>{'<!-- figure is-banner -->'}</RawHTML>}
				<div
					className='arkb-boxLink__layer'
					aria-hidden='true'
					style={layerStyle || null}
				></div>
				<IconContentV1
					{...{
						icon,
						oldSvg,
						iconSize,
						iconColor,
						iconHtml,
						useIcon,
					}}
				/>
			</>
		);
	} else if (useIcon) {
		figureContent = (
			<IconContentV1
				{...{
					icon,
					oldSvg,
					iconSize,
					iconColor,
					iconHtml,
					useIcon,
				}}
			/>
		);
	} else if (imgUrl) {
		figureContent = <RawHTML>{'<!-- figure -->'}</RawHTML>;
	}

	// タイトルがあるかどうか
	const hasTitle = !RichText.isEmpty(title);

	// リンクがあるかどうか
	const hasLink = !!href;

	const blockProps = useBlockProps.save({
		className: blockClass,
		...(hasLink
			? {
					tabIndex: '0',
					role: 'link',
					'aria-label': ariaLabel || null,
					'data-arkb-linkbox': '1',
			  }
			: {}),
	});

	const linkText = ariaLabel || (hasTitle ? title : more);

	return (
		<div {...blockProps}>
			<div className='arkb-boxLink__inner'>
				{figureContent}
				<div className='arkb-boxLink__body'>
					<RichText.Content
						tagName={htag}
						className='arkb-boxLink__title'
						value={title}
					/>
					<div className='arkb-boxLink__content ark-keep-mt--s'>
						<InnerBlocks.Content />
					</div>
					{hasLink && more && (
						<div className='arkb-boxLink__more'>
							<RichText.Content
								tagName='span'
								className='arkb-boxLink__more__text'
								value={more}
							/>
							{showMoreArrow && <RawHTML>{'<!-- more-svg -->'}</RawHTML>}
						</div>
					)}
				</div>
			</div>
			{hasLink && (
				<a
					data-arkb-link='1'
					aria-hidden='true'
					{...{
						href,
						rel: rel || null,
						target: isNewTab ? '_blank' : null,
					}}
				>
					{linkText || 'READ MORE'}
				</a>
			)}
		</div>
	);
};
export default [
	// アイコンフルリニューアル前
	{
		supports: oldSupports,
		attributes: {
			textAlign: {
				type: 'string',
				default: '',
			},
			useIcon: {
				type: 'boolean',
				default: false,
			},
			iconHtml: {
				type: 'string',
				source: 'html',
				selector: '.arkb-boxLink__figure.-html',
			},
			icon: {
				type: 'string',
				default: '',
			},
			iconSize: {
				type: 'string',
			},
			iconColor: {
				type: 'string',
			},
			layout: {
				type: 'string',
				default: 'vertical',
			},
			imgId: {
				type: 'number',
				default: 0,
			},
			imgUrl: {
				type: 'string',
			},
			imgAlt: {
				type: 'string',
			},
			imgSize: {
				type: 'string',
				default: 'full',
			},
			imgW: {
				type: 'number',
			},
			imgH: {
				type: 'number',
			},
			ratio: {
				type: 'number',
			},
			fixRatio: {
				type: 'boolean',
				default: false,
			},
			isContain: {
				type: 'boolean',
				default: false,
			},
			href: {
				type: 'string',
				source: 'attribute',
				selector: '[data-arkb-link]',
				attribute: 'href',
			},
			isNewTab: {
				type: 'boolean',
				default: false,
			},
			rel: {
				type: 'string',
				source: 'attribute',
				selector: '[data-arkb-link]',
				attribute: 'rel',
			},
			title: {
				type: 'string',
				source: 'html',
				selector: '.arkb-boxLink__title',
			},
			htag: {
				type: 'string',
				default: 'div',
			},
			opacity: {
				type: 'number',
				default: 50,
			},
			ariaLabel: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-boxLink',
				attribute: 'aria-label',
			},
			more: {
				type: 'string',
				default: 'READ MORE',
			},
			showMoreArrow: {
				type: 'boolean',
				default: true,
			},
			// 本来ないが、2.20 以前のアイコンデータを引き継ぐために追加
			iconV1: {
				type: 'string',
				source: 'html',
				selector: '.arkb-boxLink__figure.-icon',
				default: '',
			},
			// figureStyle: {
			// 	type: 'string',
			// 	source: 'attribute',
			// 	selector: '.arkb-boxLink__figure.-icon',
			// 	attribute: 'style',
			// },
		},
		migrate(attributes) {
			const newAttrs = {};
			if (attributes.icon) {
				newAttrs.icon = migrateIconName(attributes.icon);
			}
			if (Number.isSafeInteger(attributes.iconSize)) {
				newAttrs.iconSize = attributes.iconSize + 'px';
			}

			// console.log('newAttrs - 2:', newAttrs);
			return {
				...attributes,
				...newAttrs,
			};
		},
		save: saveBefore220,
	},
	// iconSize が number だった頃までサポート
	{
		supports: oldSupports,
		attributes: {
			textAlign: {
				type: 'string',
				default: '',
			},
			useIcon: {
				type: 'boolean',
				default: false,
			},
			iconHtml: {
				type: 'string',
				source: 'html',
				selector: '.arkb-boxLink__figure.-html',
			},
			icon: {
				type: 'string',
				default: '',
			},
			iconSize: {
				type: 'number',
			},
			iconColor: {
				type: 'string',
			},
			layout: {
				type: 'string',
				default: 'vertical',
			},
			imgId: {
				type: 'number',
				default: 0,
			},
			ratio: {
				type: 'number',
			},
			fixRatio: {
				type: 'boolean',
				default: false,
			},
			isContain: {
				type: 'boolean',
				default: false,
			},
			href: {
				type: 'string',
				source: 'attribute',
				selector: '[data-arkb-link]',
				// selector: 'a.arkb-boxLink__inner',
				attribute: 'href',
			},
			isNewTab: {
				type: 'boolean',
				default: false,
			},
			rel: {
				type: 'string',
				source: 'attribute',
				// selector: 'a.arkb-boxLink__inner',
				selector: '[data-arkb-link]',
				attribute: 'rel',
			},
			ariaLabel: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-boxLink',
				attribute: 'aria-label',
			},
			title: {
				type: 'string',
				source: 'html',
				selector: '.arkb-boxLink__title',
			},
			htag: {
				type: 'string',
				default: 'div',
			},
			opacity: {
				type: 'number',
				default: 50,
			},
			more: {
				type: 'string',
				default: 'READ MORE',
			},
			showMoreArrow: {
				type: 'boolean',
				default: true,
			},
			imgSize: {
				type: 'string',
				default: 'full',
			},
			imgW: {
				type: 'number',
			},
			imgH: {
				type: 'number',
			},
			imgUrl: {
				type: 'string',
			},
			imgAlt: {
				type: 'string',
			},

			// 本来ないが、2.20 以前のアイコンデータを引き継ぐために追加
			iconV1: {
				type: 'string',
				source: 'html',
				selector: '.arkb-boxLink__figure.-icon',
				default: '',
			},
		},
		migrate(attributes) {
			const newAttrs = {};
			if (attributes.icon) {
				newAttrs.icon = migrateIconName(attributes.icon);
			}

			if (Number.isSafeInteger(attributes.iconSize)) {
				newAttrs.iconSize = attributes.iconSize + 'px';
			}

			// console.log('newAttrs - 1 :', newAttrs);
			return {
				...attributes,
				...newAttrs,
			};
		},
		save: (props) => {
			return saveBefore220(props);
			// const saved = saveBefore220(props);
			// console.log(renderToString(saved));
			// return saved;
		},
	},
];
