/**
 * @WordPress dependencies
 */
import { RichText, InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import { RawHTML } from '@wordpress/element';
/**
 * @Internal dependencies
 */
import { IconContent } from './components/IconContent';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

const blockName = 'ark-block-boxLink';
export default function ({ attributes }) {
	const {
		textAlign,
		useIcon,
		icon,
		iconSize,
		iconColor,
		iconHtml,
		layout,
		imgUrl,
		title,
		htag,
		href,
		rel,
		isNewTab,
		more,
		showMoreArrow,
		opacity,
		ariaLabel,
	} = attributes;

	const blockClass = classnames(blockName, 'arkb-boxLink', '-' + layout, {
		'has-text-align-center': 'center' === textAlign,
	});

	// 縦並びか横並びかを変数化
	// const isVertical = 'vertical' === layout;

	const attrClass = attributes.className || '';
	const isBannerStyle = -1 !== attrClass.indexOf('is-style-banner');

	let figureContent = null;
	if (isBannerStyle) {
		const layerStyle = {};
		if (50 !== opacity) layerStyle.opacity = '' + (opacity * 0.01).toFixed(2);

		figureContent = (
			<>
				{imgUrl && <RawHTML>{'<!-- figure is-banner -->'}</RawHTML>}
				<div
					className='arkb-boxLink__layer'
					aria-hidden='true'
					style={layerStyle || null}
				></div>
				<IconContent {...{ icon, iconSize, iconColor, iconHtml, useIcon }} />
			</>
		);
	} else if (useIcon) {
		figureContent = <IconContent {...{ icon, iconSize, iconColor, iconHtml, useIcon }} />;
	} else if (imgUrl) {
		figureContent = <RawHTML>{'<!-- figure -->'}</RawHTML>;
	}

	// タイトルがあるかどうか
	const hasTitle = !RichText.isEmpty(title);

	// リンクがあるかどうか
	const hasLink = !!href;

	const blockProps = useBlockProps.save({
		className: blockClass,
		...(hasLink
			? {
					tabIndex: '0',
					role: 'link',
					'aria-label': ariaLabel || null,
					'data-arkb-linkbox': '1',
			  }
			: {}),
	});

	const linkText = ariaLabel || (hasTitle ? title : more);

	return (
		<div {...blockProps}>
			<div className='arkb-boxLink__inner'>
				{figureContent}
				<div className='arkb-boxLink__body'>
					<RichText.Content
						tagName={htag}
						className='arkb-boxLink__title'
						value={title}
					/>
					<div className='arkb-boxLink__content ark-keep-mt--s'>
						<InnerBlocks.Content />
					</div>
					{hasLink && more && (
						<div className='arkb-boxLink__more'>
							<RichText.Content
								tagName='span'
								className='arkb-boxLink__more__text'
								value={more}
							/>
							{showMoreArrow && <RawHTML>{'<!-- more-svg -->'}</RawHTML>}
						</div>
					)}
				</div>
			</div>
			{hasLink && (
				<a
					data-arkb-link='1'
					aria-hidden='true'
					{...{
						href,
						rel: rel || null,
						target: isNewTab ? '_blank' : null,
					}}
				>
					{linkText || 'READ MORE'}
				</a>
			)}
		</div>
	);
}
