/**
 * @WordPress dependencies
 */
import { RawHTML } from '@wordpress/element';

/**
 * @Inner dependencies
 */
import { LsIcon } from '@components';

export const IconContent = ({ icon, iconSize, iconColor, iconHtml, useIcon }) => {
	if (!useIcon) {
		return null;
	}

	const iconStyle = {};
	if (iconSize) {
		iconStyle['--arkb-boxlink_icon_size'] = iconSize;
	}
	if (iconColor) {
		iconStyle.color = iconColor;
	}

	if (!!iconHtml) {
		return (
			<figure className='arkb-boxLink__figure -icon -html' style={iconStyle || null}>
				<RawHTML>{iconHtml}</RawHTML>
			</figure>
		);
	}
	return (
		<figure className='arkb-boxLink__figure -icon' style={iconStyle || null}>
			<LsIcon icon={icon} className={`arkb-boxLink__icon`} />
		</figure>
	);
};
