/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useCallback } from '@wordpress/element';
import { ColorPalette } from '@wordpress/block-editor';
import {
	PanelBody,
	BaseControl,
	ButtonGroup,
	Button,
	ToggleControl,
	RangeControl,
	// TextareaControl,
	TextControl,
} from '@wordpress/components';

/**
 * @Internal dependencies
 */
import blockIcon from './icon';
import { ArkbImageSizeSelect, LsIconPicker, ArkbUnitNumber } from '@components';

/**
 * 設定データ
 */
const layoutBtns = [
	{
		value: 'vertical',
		label: __('Vertical', 'arkhe-blocks'),
	},
	{
		value: 'horizontal',
		label: __('Horizontal', 'arkhe-blocks'),
	},
];

// タイトル部分のタグの選択肢
const hTags = [
	{
		label: 'h2',
		val: 'h2',
	},
	{
		label: 'h3',
		val: 'h3',
	},
	{
		label: 'h4',
		val: 'h4',
	},
	{
		label: 'div',
		val: 'div',
	},
];

// const iconColorSet = [
// 	{
// 		name: __('White', 'arkhe-blocks'),
// 		color: '#fff',
// 	},
// 	{
// 		name: __('Black', 'arkhe-blocks'),
// 		color: '#000',
// 	},
// ];

/**
 * コンポーネント
 */
export default ({ attributes, isBannerStyle, setAttributes }) => {
	const {
		useIcon,
		iconHtml,
		icon,
		iconSize,
		iconColor,
		layout,
		imgId,
		imgUrl,
		imgSize,
		imgAlt,
		fixRatio,
		isContain,
		ratio,
		htag,
		more,
		showMoreArrow,
		opacity,
	} = attributes;

	// 縦並びか横並びかを変数化
	const isVertical = 'vertical' === layout;

	// 画像比率の設定
	const rationHelp = isVertical
		? __('By default, it has the same ratio as thumbnails in the archive list.', 'arkhe-blocks')
		: null;

	// サイズを変えた時の処理
	const updateImageSize = useCallback(
		(newSizeSlug, newSizeData) => {
			setAttributes({
				imgSize: newSizeSlug,
				imgUrl: newSizeData?.source_url,
				imgW: newSizeData?.width,
				imgH: newSizeData?.height,
			});
		},
		[setAttributes]
	);

	return (
		<>
			<PanelBody title={__('Box Settings', 'arkhe-blocks')} initialOpen={true}>
				<BaseControl>
					<BaseControl.VisualLabel>
						{__('Layout', 'arkhe-blocks')}
					</BaseControl.VisualLabel>
					<ButtonGroup className='arkb-btns--boxLayout'>
						{layoutBtns.map((btn) => {
							const btnVal = btn.value;
							return (
								<Button
									variant={layout === btnVal ? 'primary' : ''}
									icon={blockIcon[btnVal]}
									key={`ark-${btnVal}`}
									onClick={() => {
										setAttributes({
											layout: btnVal,
											ratio: undefined, // リセット
											fixRatio: false, // リセット
											textAlign: '', // リセット
										});
									}}
								>
									<span>{btn.label}</span>
								</Button>
							);
						})}
					</ButtonGroup>
				</BaseControl>
				<BaseControl>
					<BaseControl.VisualLabel>
						{__('HTML tag for title', 'arkhe-blocks')}
					</BaseControl.VisualLabel>
					<ButtonGroup>
						{hTags.map((btn) => {
							const isSlected = btn.val === htag;
							return (
								<Button
									// isSecondary={ ! isSlected }
									variant={isSlected ? 'primary' : ''}
									onClick={() => {
										setAttributes({ htag: btn.val });
									}}
									key={`htag_${btn.val}`}
								>
									{btn.label}
								</Button>
							);
						})}
					</ButtonGroup>
				</BaseControl>
				{isBannerStyle && (
					<RangeControl
						label={__('Overlay opacity', 'arkhe-blocks')}
						value={opacity}
						onChange={(val) => {
							setAttributes({ opacity: val });
						}}
						min={0}
						max={100}
					/>
				)}
				<div data-ark-disabled={'' === more || null}>
					<ToggleControl
						label={__('Show arrow icon', 'arkhe-blocks')}
						checked={showMoreArrow}
						onChange={(val) => {
							setAttributes({ showMoreArrow: val });
						}}
					/>
				</div>
			</PanelBody>
			{imgUrl && (
				<PanelBody title={__('Image settings', 'arkhe-blocks')} initialOpen={true}>
					<div className='u-mb-15' data-ark-disabled={isBannerStyle || null}>
						{isVertical && (
							<ToggleControl
								label={__('Fix image ratio', 'arkhe-blocks')}
								checked={fixRatio}
								onChange={(val) => {
									setAttributes({ fixRatio: val });
									if (!val) {
										setAttributes({ ratio: undefined });
									}
								}}
							/>
						)}
						{(!isVertical || fixRatio) && (
							<>
								<ToggleControl
									label={__('Show the entire image', 'arkhe-blocks')}
									checked={isContain}
									onChange={(val) => {
										setAttributes({ isContain: val });
									}}
								/>
								<RangeControl
									label={__('Image ratio', 'arkhe-blocks')}
									help={rationHelp}
									value={ratio}
									onChange={(val) => {
										setAttributes({ ratio: val });
									}}
									min={1}
									max={100}
									allowReset={true}
									className='arkb-range--useReset'
								/>
							</>
						)}
					</div>
					<ArkbImageSizeSelect
						imgId={imgId}
						imgSize={imgSize}
						onChange={updateImageSize}
					/>
					<TextControl
						label={__('Alt text', 'arkhe-blocks')}
						value={imgAlt || ''}
						onChange={(val) => {
							setAttributes({ imgAlt: val });
						}}
					/>
				</PanelBody>
			)}

			<PanelBody title={__('Icon settings', 'arkhe-blocks')} initialOpen={true}>
				<ToggleControl
					label={__('Use icon', 'arkhe-blocks')}
					checked={useIcon}
					onChange={(val) => {
						setAttributes({ useIcon: val });
						if (val && !icon) {
							setAttributes({ icon: 'LsLightbulb' });
						}
					}}
				/>
				{useIcon && (
					<>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Select Icon', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<LsIconPicker
								value={icon}
								svg={iconHtml}
								onChange={(val) => {
									setAttributes({ icon: val, iconHtml: undefined });
								}}
								onSetSvg={(val) => {
									setAttributes({ iconHtml: val, icon: '' });
								}}
								clearable={false}
							/>
						</BaseControl>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Icon size', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ArkbUnitNumber
								value={iconSize || '5em'}
								units={['px', 'em']}
								onChange={(val) => {
									if ('5em' === val) {
										setAttributes({ iconSize: undefined });
									} else {
										setAttributes({ iconSize: val });
									}
								}}
								onClear={() => setAttributes({ iconSize: undefined })}
								// default={}
							/>
						</BaseControl>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Icon color', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ColorPalette
								value={iconColor}
								// colors={iconColorSet}
								onChange={(val) => {
									setAttributes({ iconColor: val });
								}}
							/>
						</BaseControl>
					</>
				)}
			</PanelBody>
		</>
	);
};
