/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useMemo, useCallback, useState, useEffect } from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import {
	RichText,
	BlockControls,
	InspectorControls,
	useBlockProps,
	MediaReplaceFlow,
} from '@wordpress/block-editor';
import { ToolbarButton, ToolbarGroup } from '@wordpress/components';
import { alignCenter, closeSmall } from '@wordpress/icons';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import blockIcon from './icon';
import TheSidebar from './_sidebar';
import { Figure } from './components/Figure';
import { IconContent } from './components/IconContent';
import { ArkbLinkControl } from '@components';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

/**
 * export edit
 */

const ALLOWED_BLOCKS = ['core/paragraph', 'core/list', 'core/buttons', 'arkhe-blocks/buttons'];
const blockName = 'ark-block-boxLink';
export default ({ attributes, setAttributes }) => {
	const {
		textAlign,
		href,
		rel,
		isNewTab,
		ariaLabel,
		useIcon,
		icon,
		iconSize,
		iconColor,
		iconHtml,
		layout,
		imgId,
		imgUrl,
		imgAlt,
		imgSize,
		fixRatio,
		isContain,
		ratio,
		title,
		opacity,
		more,
		showMoreArrow,
	} = attributes;

	const attrClass = attributes.className || '';
	const isBannerStyle = -1 !== attrClass.indexOf('is-style-banner');

	// 縦並びか横並びかを変数化
	const isVertical = 'vertical' === layout;

	useEffect(() => {
		if (isBannerStyle) {
			setAttributes({ fixRatio: false });
			setAttributes({ isContain: false });
		}
	}, [isBannerStyle]);

	// state
	const [isLinkOpen, setIsLinkOpen] = useState(false);

	// 画像選択時の処理
	const onSelectImage = useCallback(
		(media) => {
			// console.log(media);

			if (!media || !media.url) {
				onRemoveImage();
				return;
			}

			const hasSizeData = !!media.sizes && !!media.sizes[imgSize];
			const theSizeData = hasSizeData ? media.sizes[imgSize] : [];

			const newImageURL = theSizeData.url || media.url;
			const newImageWidth = theSizeData.width || media.width;
			const newImageHeight = theSizeData.height || media.height;

			setAttributes({
				imgId: media.id,
				imgUrl: newImageURL,
				imgAlt: media.alt,
				imgW: newImageWidth,
				imgH: newImageHeight,
			});
		},
		[imgSize]
	);

	// メディアじゃなくてURLを直接入力した時
	const onSelectURL = useCallback(
		(newURL) => {
			if (newURL !== imgUrl) {
				setAttributes({
					imgSize: 'full',
					imgUrl: newURL,
					imgId: 0,
					imgW: '',
					imgH: '',
					imgAlt: '',
				});
			}
		},
		[imgUrl]
	);

	// 画像削除時の処理（attributesを初期値に戻す）
	const onRemoveImage = useCallback(() => {
		setAttributes({
			imgId: 0,
			imgUrl: undefined,
			imgAlt: undefined,
			imgW: undefined,
			imgH: undefined,
		});
	}, []);

	const figureContent = useMemo(() => {
		if (isBannerStyle) {
			return (
				<>
					<Figure
						url={imgUrl}
						id={imgId}
						alt={imgAlt}
						figureClass='arkb-boxLink__bg'
						figureStyle={null}
						imgClass='arkb-boxLink__img arkb-obf-cover'
						onSelect={onSelectImage}
						onSelectURL={onSelectURL}
					/>
					<div
						className='arkb-boxLink__layer'
						aria-hidden='true'
						style={{ opacity: '' + (opacity * 0.01).toFixed(2) }}
					></div>
					<IconContent {...{ icon, iconSize, iconColor, iconHtml, useIcon }} />
				</>
			);
		} else if (useIcon) {
			return <IconContent {...{ icon, iconSize, iconColor, iconHtml, useIcon }} />;
		}

		// figure の style
		const figureStyle = {};
		if (isVertical && ratio) {
			// figureStyle.paddingTop = `${ratio}%`;
			figureStyle['--ark-thumb_ratio'] = `${ratio}%`;
		} else if (!isVertical && ratio) {
			figureStyle.flexBasis = `${ratio}%`;
		}

		return (
			<Figure
				url={imgUrl}
				id={imgId}
				alt={imgAlt}
				figureClass={classnames('arkb-boxLink__figure', { 'is-fixed-ratio': fixRatio })}
				figureStyle={figureStyle || null}
				imgClass={classnames('arkb-boxLink__img', {
					'arkb-obf-cover': (fixRatio || !isVertical) && !isContain,
					'arkb-obf-contain': (fixRatio || !isVertical) && isContain,
				})}
				onSelect={onSelectImage}
				onSelectURL={onSelectURL}
			/>
		);
	}, [attributes, isBannerStyle, onSelectImage, onSelectURL]);

	// リンクを除外
	const allowedFormatTypes = useSelect((select) => {
		const allFormatTypes = select('core/rich-text').getFormatTypes();
		// map で format.name の配列を返し、filter で 'core/link' を除外
		return allFormatTypes.map((format) => format.name).filter((name) => name !== 'core/link');
	}, []);

	// ブロッククラス
	const blockClass = classnames(blockName, 'arkb-boxLink', '-' + layout, {
		'has-text-align-center': 'center' === textAlign,
	});

	// props
	const blockProps = useBlockProps({
		className: blockClass,
		'data-has-icon': !!href,
	});

	const innerBlocksProps = useInnerBlocksProps(
		{ className: 'arkb-boxLink__content ark-keep-mt--s' },
		{
			allowedBlocks: ALLOWED_BLOCKS,
			template: [['core/paragraph']],
			templateLock: false,
		}
	);

	const moreTextProps = !href
		? {
				className: 'arkb-boxLink__more is-inactive',
				role: 'button',
				tabIndex: '0',
				// onKeyDown: () => {},
				onClick: () => {
					setIsLinkOpen(true);
				},
		  }
		: {
				className: `arkb-boxLink__more is-empty-${RichText.isEmpty(more)}`,
		  };

	const setLink = (changedVal) => {
		const { url, rel: newRel, opensInNewTab } = changedVal;

		if (url) {
			setAttributes({ href: url || undefined });
		}

		if (typeof opensInNewTab !== 'undefined') {
			setAttributes({
				isNewTab: opensInNewTab,
				rel: newRel,
			});
		}
	};

	const removeLink = () => {
		setAttributes({
			href: undefined,
			isNewTab: false,
			rel: undefined,
			ariaLabel: undefined,
		});
	};

	return (
		<>
			<BlockControls>
				{isVertical && (
					<ToolbarGroup>
						<ToolbarButton
							className={classnames('components-toolbar__control', {
								'is-pressed': 'center' === textAlign,
							})}
							label={__('Center the text', 'arkhe-blocks')}
							icon={alignCenter}
							onClick={() => {
								const newAlign = 'center' === textAlign ? '' : 'center';
								setAttributes({ textAlign: newAlign });
							}}
						/>
					</ToolbarGroup>
				)}
				<ArkbLinkControl
					attrs={{ url: href, rel, isNewTab, ariaLabel }}
					setLink={setLink}
					isLinkOpen={isLinkOpen}
					setIsLinkOpen={setIsLinkOpen}
					removeLink={removeLink}
					setRel={(val) => {
						setAttributes({ rel: val || undefined });
					}}
					setAriaLabel={(val) => {
						setAttributes({ ariaLabel: val || undefined });
					}}
				/>
				{!!imgUrl && (
					<>
						<ToolbarGroup>
							<MediaReplaceFlow
								mediaId={imgId}
								mediaURL={imgUrl}
								allowedTypes={['image']}
								accept='image/*'
								onSelect={onSelectImage}
								onSelectURL={onSelectURL}
							/>
							<ToolbarButton
								className='components-toolbar__control'
								label={__('Delete image', 'arkhe-blocks')}
								icon={closeSmall}
								onClick={onRemoveImage}
							/>
						</ToolbarGroup>
					</>
				)}
			</BlockControls>
			<InspectorControls>
				<TheSidebar
					{...{
						attributes,
						isBannerStyle,
						setAttributes,
					}}
				/>
			</InspectorControls>
			<div {...blockProps}>
				<div className='arkb-boxLink__inner'>
					{figureContent}
					<div className='arkb-boxLink__body'>
						<RichText
							tagName='div'
							className={`arkb-boxLink__title is-empty-${RichText.isEmpty(title)}`}
							placeholder={__('Enter text', 'arkhe-blocks') + '...'}
							value={title}
							allowedFormats={allowedFormatTypes}
							onChange={(newTitle) => setAttributes({ title: newTitle })}
						/>
						<div {...innerBlocksProps} />
						<div {...moreTextProps}>
							<RichText
								tagName='div'
								className='arkb-boxLink__more__text'
								placeholder={__('Enter text', 'arkhe-blocks') + '...'}
								value={!!href ? more : 'Add Link'}
								allowedFormats={[]}
								onChange={(newText) => setAttributes({ more: newText })}
							/>
							{showMoreArrow && blockIcon.moreArrow}
						</div>
					</div>
				</div>
			</div>
		</>
	);
};
