/**
 * @WordPress dependencies
 */
import { RawHTML } from '@wordpress/element';

/**
 * @Inner dependencies
 */
import { LsIcon } from '@components';

export default ({ icon, oldSvg, iconSize, iconColor, iconHtml, useIcon }) => {
	if (!useIcon) {
		return null;
	}

	const iconStyle = {};
	if (iconSize) {
		if (Number.isSafeInteger(iconSize)) {
			iconStyle['--arkb-boxlink_icon_size'] = iconSize + 'px';
		} else {
			iconStyle['--arkb-boxlink_icon_size'] = iconSize;
		}
	}
	if (iconColor) {
		iconStyle.color = iconColor;
	}

	if (!!iconHtml) {
		return (
			<figure className='arkb-boxLink__figure -icon -html' style={iconStyle || null}>
				<RawHTML>{iconHtml}</RawHTML>
			</figure>
		);
	}

	let theIcon = null;
	if (oldSvg) {
		theIcon = <RawHTML>{oldSvg}</RawHTML>;
	} else if (icon) {
		theIcon = <LsIcon icon={icon} className={`arkb-boxLink__icon`} />;
	}

	return (
		<figure className='arkb-boxLink__figure -icon' style={iconStyle || null}>
			{theIcon}
		</figure>
	);
};
